<template>
  <v-layout row wrap>
    <v-flex xs12 sm12 md4>
      <v-card flat>
        <v-card-title>Options</v-card-title>
        <v-card-text class="d-inline-flex">
          <!-- POINTS MENU -->
          <v-scoremenu
            :selected=options.point
            :obj=points
            v-on:update:selected="options.point = $event.title; values.point = $event.value"
          />
          <!-- BONUS MENU -->
          <v-scoremenu
            :selected=options.bonus
            :obj=bonus
            v-on:update:selected="options.bonus = $event.title; values.bonus = $event.value"
          />

          <v-btn
            @click="calculate()"
            color="primary"
          >
            Calculate
          </v-btn>
        </v-card-text>
      </v-card>
    </v-flex>
    <v-flex xs12 sm12 md8>
      <v-card flat height="100%">
        <v-card-text dense>
          <div class="d-inline-flex">
            <v-text-field
                v-model="values.total"
                box
                disabled
                label="Total"
            />
            <v-text-field
                v-model="values.diff"
                box
                disabled
                label="Difference"
            />
          </div>
          <div class="d-flex">
            <v-text-field
              v-model="players.player1"
              outline
              type="number"
              label="Player 1"
            />
            <v-text-field
                v-model="players.player2"
                outline
                type="number"
                label="Player 2"
            />
            <v-text-field
                v-model="players.player3"
                outline
                type="number"
                label="Player 3"
            />
            <v-text-field
                v-model="players.player4"
                outline
                type="number"
                label="Player 4"
            />
          </div>
          <div class="d-flex">
            <v-text-field
                v-model="values.score.player1"
                box
                disabled
                label="Score"
            />
            <v-text-field
                v-model="values.score.player2"
                box
                disabled
                label="Score"
            />
            <v-text-field
                v-model="values.score.player3"
                box
                disabled
                label="Score"
            />
            <v-text-field
                v-model="values.score.player4"
                box
                disabled
                label="Score"
            />
          </div>
        </v-card-text>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import ScoreMenu from '@/components/tools/ScoreMenuComponent.vue';

export default {
  components: {
    'v-scoremenu': ScoreMenu
  },
  data: () => ({
    players: {
      player1: undefined,
      player2: undefined,
      player3: undefined,
      player4: undefined
    },
    options: {
      point: 'Points',
      bonus: 'Uma'
    },
    values: {
      point: undefined,
      bonus: undefined,
      total: 0,
      diff: 0,
      score: {
        player1: undefined,
        player2: undefined,
        player3: undefined,
        player4: undefined
      }
    },
    points: [
      { title: '25000', value: 25000 },
      { title: '30000', value: 30000 },
    ],
    bonus: [
      { title: '5-10', value: [10,5,-5,-10] },
      { title: '5-15', value: [15,5,-5,-15] }
    ]
  }),
  methods: {
    calculate() {
      this.values.total = this.values.point * 4
      this.values.diff = (
          Number(this.players.player1) +
          Number(this.players.player2) +
          Number(this.players.player3) +
          Number(this.players.player4)
      ) - this.values.total;
      this.values.score = Object.entries(this.players)
        .sort(([,a],[,b]) => b - a)
        .map((e, i) => [
          e[0],
          Math.round(((e[1] - this.values.point) / 1000 + this.values.bonus[i]) * 10) / 10
        ])
        .reduce((r, [k, v]) => ({ ...r, [k]: v }), {})
    }
  }
}
</script>

<style>
.right-border {
  border-right: 1px solid rgba(0,0,0,.12);
}
.bottom-border {
  border-bottom: 1px solid rgba(0,0,0,.12);
}
</style>
