<template>
  <v-menu
    offset-y
    transition="slide-y-transition"
  >
    <template v-slot:activator="{ on }">
      <v-btn v-on="on">
        {{ selected }}
      </v-btn>
    </template>
    <v-list>
      <v-list-tile
        v-for="(item, i) in obj"
        :key="i"
        @click=update(item)
      >
        <v-list-tile-title>{{ item.title }}</v-list-tile-title>
      </v-list-tile>
    </v-list>
  </v-menu>
</template>

<script>
export default {
  methods: {
    update(input) {
      this.$emit('update:selected', input)
    }
  },
  props: [
    'selected',
    'obj',
  ]
}
</script>
